@import '~antd/dist/antd.css';

// variables

// ant gray scale
$white0: #ffffff;
$white1: #fafafa;
$white2: #f5f5f5;
$white3: #f0f0f0;
$black0: #d9d9d9;
$black1: #bfbfbf;
$black2: #8c8c8c;
$black3: #595959;
$black4: #434343;
$black5: #262626;
$black6: #1f1f1f;
$black7: #141414;
$black8: #000000;

// tri chromatic color scheme
$color0: #1890ff;
$color1: #1abc9c;
$color2-light: #389e0d;
$color2-dark: #d9f7be;

$left: 20px;
$menu-height: 10px;

@mixin base {

  .button {

    margin: 0px 5px 0px 5px;
    padding: 5px;
    border: 1px solid;
    border-radius: 5px;

  }

  .social-networks .ant-btn {

    filter: grayscale(0.7);

    border-radius: 10px;
    border: 2px solid;

    &:hover {
      border-color: $color0;
      filter: unset;
    }

  }

}

@mixin light-base {

  background-color: $white0;
  color: $black8;

  $outline-color: $black3;

  .ribbon {

    background-color: $white3;
    border-color: $outline-color;

    .menu {

      a {

        color: $black2;

        &:hover {
          color: $black8;
        }

      }

      .selected {

        a {

          color: $black8;

        }
        
      }
      
    }

  }
  
  .complete {

    &.false {
      
      color: $black3;

    }

    &.in-progress {

      color: $color2-light;

    }

  }

  .button, .social-networks .ant-btn {

    background-color: $white3;
    border-color: $outline-color;

  }

  .information-card {

    border-color: $outline-color;

    .img-container {
      background-color: $black1;
      border-color: $outline-color;
    }

    .icon-container {

      .flag {
        border-color: $outline-color;
      }

      .icon {
        color: $outline-color;
      }
      
    }

    .genre {

      background-color: $white3;
      
    }
  }

  .ant-col {

    img {

      border-color: $outline-color;

    }

  }

}

@mixin dark-base {

  background-color: $black6;
  color: $white0;

  $outline-color: $black1;

  .ribbon {

    background-color: $black5;
    border-color: $outline-color;

    .menu {

      a {

        color: $black2;

        &:hover {
          color: $white0;
        }

      }

      .selected {

        a {

          color: $white0;

        }
        
      }
      
    }

  }

  .complete {

    &.false {
      
      color: $black1;

    }

    &.in-progress {

      color: $color2-dark;
      
    }

    
  }

  .button, .social-networks .ant-btn {

    background-color: $black4;
    border-color: $outline-color;

  }

  .information-card {

    border-color: $outline-color;

    .img-container {

      background-color: $black4;
      border-color: $outline-color;

    }

    .icon-container {

      .flag {
        border-color: $outline-color;
      }

      .icon {
        color: $outline-color;
      }
    }

    .genre {

      background-color: $black5;

    }
  }

  .ant-col {
    
    img {

      border-color: $outline-color;

    }

  }

}

@mixin horizontal-menu {
  
  ul {
      
      padding-top: 10px;
      
  }

  li {

      // make it a horizontal list
      display: inline; 
      padding-right: 50px;

  }
}

body {

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &.light-theme {

    @include base;
    @include light-base;

  }

  &.dark-theme {

    @include base;
    @include dark-base;

  }

}

@mixin heading {

  color: $color0;
  
}

h1 {

  @include heading;
}

h2 {

  @include heading;

}

h3 {

  @include heading;

}

h4 {

  @include heading;


}

code {
  color: $color1;
}

.clickable {

  cursor: pointer;
  
}

.highlight {

  color: $color1;

  &:hover {

      font-weight: bold;

  }
}